* {
    box-sizing: border-box;
    letter-spacing: 1px;
    
}

.loginForm {
    padding: 3vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.loginForm__title{
    margin-bottom: 2vh;
    font-size: 2.5vmax;
}

.loginForm__input{
    font-size: 1.8vmax;
    padding: .2vmax;
}

.loginForm__input, .loginForm__submit{
    margin-bottom: 1vh;
}